/*字体size*/
.font12 {font-size: 12px !important;
}
.font14 {font-size: 14px !important;
}
.font16 {font-size: 16px !important;
}
.font18 {font-size: 18px !important;
}
.font20 {font-size: 20px !important;
}
/*浮动*/
.fl{display: inline; float: left
}
.fr{display: inline; float: right
}
/*消失隐藏*/
.show{display: block!important;
}
.hide{display: none!important;
}
/*文本超出隐藏*/
.oe1{ overflow: hidden; white-space: nowrap; text-overflow: ellipsis;
}
/*单行*/
.oe2{ display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 2; overflow: hidden;
}
/*两行*/
.oe3{ display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 3; overflow: hidden;
}
